.footer {
  width: 100%;
  background: #002B45;
  color: #ffffff;
  padding: 6.25rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
    margin-right: 2rem;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    width: 11.0625rem;
    display: block;
    margin-right: 2rem;
    img {
      width: 100%;
    }
  }
  &__yandex {
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 0;
      width: 3.4375rem;
      margin-right: 0.3125rem;
      img {
        width: 100%;
      }
    }
    &-title {
      font-size: 13.826px;
      line-height: 130%;
      font-weight: 700;
    }
    &-rating {
      display: flex;
      align-items: center;
      margin-top: 0.125rem;
    }
    .stars {
      display: flex;
      align-items: center;
    }
    .star {
      width: 1.125rem;
      display: flex;
      align-items: center;
    }
    .star img {
      width: 100%;
    }
    &-link {
      display: block;
      font-size: 0.5625rem;
      line-height: 130%;
      margin-top: 0.125rem;
    }
  }
  &__rating-text {
    font-size: 1.3125rem;
    line-height: 130%;
    font-weight: 700;
    color: #827F7F;
    margin-left: 0.1875rem;
  }
  &__location {
    font-size: 0.875rem;
    line-height: 130%;
    margin-right: 3.125rem;
    span {
      display: block;
      line-height: 130%;
      font-weight: 700;
      margin-bottom: 0.3125rem;
    }
  }
  &__socials {
    text-align: center;
    margin-right: 3.125rem;
  }
  &__socials-wrap {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-top: 1rem;
  }
  &__socials-title {
    font-size: 0.875rem;
    line-height: 130%;
  }
  &__social {
    width: 2.25rem;
    height: 2.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffff;
    border-radius: 50%;
    svg {
      max-width: 1.4375rem;
      width: auto;
      height: auto;
    }
    svg path {
      transition: all 0.2s;
    }
    &:hover {
      background: #F2A22E;
      svg path {
        fill: #ffffff;
      }
    }
  }
  .whatsapp {
    &:after {
      border: 0.375rem solid transparent;
      border-top: 0.75rem solid #ffffff;
    }
    &:hover {
      &:after {
        border-top-color: #F2A22E;
      }
    }
  }
  &__group {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__phone {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    &:hover {
      color: #F2A22E;
    }
  }
  &__btn {
    position: relative;
    background: transparent;
    border: none;
    padding: 0 0 0 0.75rem;
    margin-top: 0.5rem;
    &:after {
      content: '';
      position: absolute;
      top: 0.5rem;
      left: 0;
      width: 0.375rem;
      height: 0.375rem;
      background: #ffffff;
      filter: drop-shadow(0px 0px 5.7px #FBF6EF);
      border-radius: 50%;
      transition: all 0.3s;
    }
    span {
      display: inline;
      font-size: 0.875rem !important;
      line-height: 130%;
      border-bottom: 0.0625rem dashed #ffffff;
      color: #ffffff;
      transition: all 0.3s;
    }
    &:hover {
      &:after {
        background: #F2A22E;
      }
      span {
        border-color: #F2A22E;
        color: #F2A22E;
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .footer {
    &__location {
      margin-right: 2rem;
    }
    &__socials {
      margin-right: 2rem;
    }
  }
}

@media screen and (max-width: 1300px) {
  .footer {
    padding: 3.125rem 0;
    @media screen and (min-width: 993px) {
      &__inner {
        align-items: start;
      }
      &__left {
        display: block;
      }
      &__logo {
        margin: 0 0 2rem 0;
      }
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer {
    &__inner {
      display: block;
    }
    &__left {
      margin: 0 0 2rem 0;
    }
    &__right {
      justify-content: space-between;
    }
    &__social {
      width: 2rem;
      height: 2rem;
      svg {
        max-width: 1.125rem;
      }
    }
    &__phone {
      font-size: 1.125rem;
    }
  }
}

@media screen and (max-width: 767px) {
  .footer {
    &__left {
      display: block;
    }
    &__right {
      display: block;
    }
    &__logo {
      margin:  0 0 2rem 0;
    }
    &__location {
      margin: 0 0 2rem 0;
    }
    &__socials {
      text-align: start;
      margin: 0 0 2rem 0;
    }
    &__group {
      align-items: start;
    }
  }
}