html {
  font-size: max(calc(16px * var(--coef_h)), 15px);
}
body {
  font-family: 'OpenSans', sans-serif;
}
.fixed-body {
  overflow: hidden;
}
.wrapper {
  overflow: hidden;
}
@media (min-width: 1400px) {
  .container-fluid {
    max-width: 1815px;
  }
}

img {
  object-fit: cover;
}