.main {
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: revert;
  background-size: cover;
  color: #ffffff;
  padding: 18.125rem 0 10rem 0;
  &__inner {
    position: relative;
  }
  &__wrap {
    max-width: 45.625rem;
  }
  h1 {
    font-size: 4rem;
    line-height: 130%;
    font-weight: 400;
  }
  &__price {
    display: flex;
    align-items: center;
    margin-bottom: 3.5rem;
    &-icon {
      flex-shrink: 0;
      width: 4.375rem;
      margin-right: 2rem;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 1.75rem;
      line-height: 130%;
    }
    &-text strong {
      font-weight: 700;
    }
  }
  &__info {
    position: relative;
    display: inline-flex;
    margin-bottom: 0.9375rem;
    span {
      font-size: 0.875rem;
      line-height: 130%;
    }
    i {
      position: absolute;
      top: 0.5625rem;
      right: -2.625rem;
      width: 2.375rem;
      img {
        width: 100%;
      }
    }
  }
  &__btn {
    width: 12.6875rem;
    height: 3.625rem;
  }
  &__advantage {
    position: relative;
    min-height: 5.375rem;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 43, 69, 0.90);
    backdrop-filter: blur(0.125rem);
    border-radius: 0.9375rem;
    padding: 0.1875rem 2rem;
    &-text {
      font-size: 1.125rem;
      line-height: 130%;
    }
    &-icon {
      position: absolute;
      top: 50%;
      right: -1rem;
      transform: translate(0, -50%);
      width: 2rem;
      height: 2rem;
      background: #FBF6EF;
      backdrop-filter: blur(2px);
      border-radius: 50%;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 1.25rem;
        height: 1.25rem;
        background: rgba(255, 255, 255, 0.68);
        box-shadow: 0 0.25rem 0.25rem 0 rgba(0, 0, 0, 0.25);
        border-radius: 50%;

      }
    }
  }
  &__advantage:nth-child(1) {
    position: absolute;
    top: -6.875rem;
    left: 25rem;
  }
  &__advantage:nth-child(2) {
    position: absolute;
    top: 3.5rem;
    left: 52.625rem;
  }
  &__advantage:nth-child(3) {
    position: absolute;
    top: 16rem;
    left: 48rem;
  }
}
.advantages {
  padding: 0 0 2.1875rem 0;
  margin-top: -2.5rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin-left: -9.375rem;
  }
  &__left {
    width: 26.25rem;
  }
  &__right {
    position: relative;
    width: calc(100% - 26.25rem);
    display: flex;
    align-items: center;
    padding-left: 5.625rem;
    &:after {
      content: '';
      position: absolute;
      top: 2.5rem;
      left: 0;
      width: 312.5rem;
      height: calc(100% - 7.5rem);
      background: #FBF6EF;
    }
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__desc {
    position: relative;
    z-index: 2;
    padding: 6.25rem 0;
    margin-top: -2.5rem;
  }
  h2 {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 1.375rem;
      left: calc(100% + 1.25rem);
      width: 312.5rem;
      height: 0.0625rem;
      background: #002B45;
    }
  }
  &__items {
    display: flex;
    justify-content: space-between;
    gap: 4.375rem;
    margin-top: 3.5rem;
  }
  &__item {
    display: flex;
    &-icon {
      flex-shrink: 0;
      width: 4.375rem;
      margin-right: 1.875rem;
      img {
        width: 100%;
      }
    }
    &-text {
      max-width: 16.875rem;
      font-size: 1.5rem;
      line-height: 130%;
      color: #002B45;
      span {
        display: block;
        font-weight: 700;
      }
    }
  }
  &__item:nth-child(2) {
    transition: all 0.7s;
  }
  &__item:nth-child(3) {
    transition: all 0.9s;
  }
}
.works {
  position: relative;
  z-index: 4;
  padding: 2.1875rem 0 25rem 0;
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 70rem;
    background: #002B45;
    pointer-events: none;
  }
  .container-fluid {
    position: relative;
    z-index: 2;
  }
  &__top {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 3.125rem;
  }
  .close-menu {
    display: none;
  }
  &__selections {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-right: 3.125rem;
    margin-bottom: 1.5rem;
  }
  .select__wrap {
    position: relative;
    width: 13.125rem;
  }
  .select__wrap--active {
    .select__list-default {
      color: #ffffff;
      background: #002B45;
    }
    .select-arrow {
      svg {
        transform: rotate(-180deg);
      }
    }
    .select__list {
      opacity: 1;
      pointer-events: all;
    }
  }
  .select__list-default {
    height: 3.625rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #002B45;
    background: #ffffff;
    border: 0.0625rem solid #002B45;
    padding: 0.1875rem 1.25rem;
    cursor: pointer;
    transition: all 0.3s;
  }
  .select__current {
    font-size: 1rem;
    line-height: 130%;
    text-transform: uppercase;
    input {
      display: none;
    }
  }
  .select-arrow {
    flex-shrink: 0;
    width: 1.4375rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 0.625rem;
    svg {
      width: 100%;
      height: auto;
      transition: all 0.3s;
    }
    svg path {
      fill: currentColor;
    }
  }
  .select__list {
    position: absolute;
    z-index: 3;
    top: 100%;
    left: 0;
    width: 100%;
    padding-top: 1.25rem;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  .select__checkboxes {
    display: grid;
    grid-template-columns: 100%;
    gap: 1rem;
    background: #FFF;
    box-shadow: 0 0.25rem 0.4375rem 0.375rem rgba(0, 0, 0, 0.05);
    padding: 1.5625rem 1.25rem;
  }
  .select__check {
    position: relative;
    max-width: 13.125rem;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
      cursor: pointer;
    }
    input:checked ~ label {
      &:before {
        opacity: 1;
        pointer-events: all;
      }
    }
    label {
      position: relative;
      padding-left: 1.5rem;
      &:after {
        content: '';
        position: absolute;
        top: 0.3125rem;
        left: 0;
        width: 0.875rem;
        height: 0.875rem;
        background: transparent;
        border: 0.0625rem solid #002B45;
        border-radius: 0.1875rem;
      }
      &:before {
        content: '';
        position: absolute;
        top: 0.5rem;
        left: 0.1875rem;
        width: 0.5rem;
        height: 0.5rem;
        background: #002B45;
        border-radius: 0.1875rem;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 1.5rem;
  }
  &__btn {
    height: 3.625rem;
    background: #E1E1E1;
    padding: 0.1875rem 2rem;
    border: none;
    span {
      font-size: 1rem;
      line-height: 130%;
      text-transform: uppercase;
      color: #002B45;
      padding: 0;
    }
    &:hover {
      background: #cbcbcb;
    }
  }
  &__btn2 {
    height: 3.625rem;
    background: #ffffff !important;
    padding: 0.1875rem 2rem;
  }
  &__filter {
    display: none;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
    margin-top: 2.5rem;
    span {
      font-size: 1.5rem;
      line-height: 130%;
      font-weight: 700;
      color: #002B45;
    }
    i {
      flex-shrink: 0;
      width: 2rem;
      margin-left: 1rem;
      img {
        width: 100%;
      }
    }
  }
  &__cards {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 3rem / 2));
    gap: 3rem;
    margin-top: 1.5625rem;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -6.875rem;
      width: 34.375rem;
      height: 15.9375rem;
      border: 0.0625rem solid #FBF6EF;
      pointer-events: none;
    }
  }
  &__link {
    position: relative;
    z-index: 2;
    width: 13.75rem;
    height: 3.625rem;
    background: #002B45 !important;
    margin: 3.625rem auto 0;
  }
  &__lines {
    span:nth-child(1) {
      position: absolute;
      top: -0.625rem;
      right: -0.625rem;
      width: 47.8125rem;
      height: 20.3125rem;
      border: 0.0625rem solid #002B45;
      pointer-events: none;
    }
  }
  &__text-icon {
    position: absolute;
    z-index: 1;
    right: 1.75rem;
    bottom: 3rem;
  }
  .filter-bg {
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &--active {
      opacity: 1;
      pointer-events: all;
    }
  }
}
.works-card {
  display: flex;
  align-items: center;
  color: #002B45;
  &__slider {
    position: relative;
    z-index: 2;
    width: 17.9375rem;
  }
  &__desc {
