  }
  &__btn {
    width: 10rem;
    height: 3.625rem;
    cursor: pointer !important;
  }
  &__btn2 {
    flex-grow: 1;
    height: 3.625rem;
  }
  .big-slider {
    .works-card__img {
      position: relative;
      padding-bottom: 126%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
      }
    }
  }
  .small-slider {
    position: relative;
    background: #001623;
    padding: 0.75rem 1.25rem 0.75rem 4.6875rem;
    .slick-slide {
      &:not(:last-child) {
        margin-right: 0.375rem;
      }
    }
    .works-card__img {
      position: relative;
      padding-bottom: 90%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
      }
    }
  }
  .small-slider__wrap {
    position: unset;
  }
  .slick-list {
    padding: 0 !important;
  }
  .slick-track {
    display: flex;
    align-items: stretch;
  }
  .slick-slide {
    height: auto;
    div:first-child {
      height: 100%;
    }
  }
  &__img {
    height: 100%;
    display: block !important;
  }
  .slick-prev {
    top: 70%;
    left: 1rem;
    transform: translate(0, -50%);
    width: 1.1875rem;
    height: auto;
    box-shadow: unset;
    padding: 0;
    &:after {
      width: 100%;
      height: 0.4375rem;
    }
  }
  .slick-next {
    top: 70%;
    left: 2.5rem;
    transform: translate(0, -50%);
    width: 1.1875rem;
    height: auto;
    box-shadow: unset;
    padding: 0;
    &:after {
      width: 100%;
      height: 0.4375rem;
    }
  }
  .slick-dots {
    display: none !important;
  }
  .pagingInfo {
    position: absolute;
    top: 50%;
    left: 1.25rem;
    transform: translate(0, -50%);
    font-family: 'Lora';
    span:nth-child(1) {
      font-size: 1.25rem;
      line-height: 130%;
      color: #ffffff;
    }
    span:nth-child(2) {
      font-size: 0.625rem;
      line-height: 130%;
      color: #797979;
      margin-left: 0.125rem;
    }
  }
}
.section-wrap {
  position: relative;
  &:after {
    content: '';
