    }
  }
  .stages {
    &__items {
      grid-template-columns: 100%;
      gap: 2rem;
    }
    &__item {
      display: flex;
      &-icon {
        flex-shrink: 0;
      }
      &-desc {
        max-width: 100%;
        margin: 0 0 0 1.125rem;
      }
    }
  }
  .stocks {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .stock-card {
    &__form {
      display: block;
      padding: 1rem;
      &-socials {
        margin: 1rem 0 0 0;
      }
    }
  }
  .videos {
    &-small__wrap {
      padding: 4rem 1rem 5rem 2.125rem;
    }
    .slick-prev {
      top: unset;
      left: 44%;
      bottom: 1.75rem;
      transform: translate(-50%, 0);
    }
    .slick-next {
      top: unset;
      left: 56%;
      bottom: 1.75rem;
      transform: translate(-50%, 0);
    }
    .pagingInfo {
      top: unset;
      left: 50%;
      bottom: 2.5rem;
      transform: translate(-50%, 0);
    }
  }
}

@media screen and (max-width: 359px) {
  .advantages2 {
    &__flex {
      padding-left: 1rem;
      padding-right: 1rem;
    }
  }
}