.gratitude {
  padding: 16.25rem 0 8.125rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    position: relative;
    flex-shrink: 0;
    width: 44.375rem;
    padding: 10.3125rem 0 10.3125rem 10.3125rem;
    margin-right: 3.125rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 312.5rem;
      height: 100%;
      background: #FBF6EF;
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 18.125rem;
      transform: translate(0, -50%);
      width: 50rem;
      height: calc(100% + 8.75rem);
      border: 0.0625rem solid #002B45;
      pointer-events: none;
    }
  }
  &__right {
    width: 100%;
    max-width: 41.875rem;
    margin: 0;
  }
  &__desc {
    position: relative;
    z-index: 2;
  }
  h1 {
    font-family: 'Lora';
    font-size: 2.5rem;
    line-height: 130%;
    color: #333;
    margin: 0;
  }
  &__text {
    max-width: 31.875rem;
    font-size: 1.5rem;
    line-height: 130%;
    color: #333;
    margin-top: 1.5rem;
  }
  &__btn {
    width: 16.25rem;
    height: 3.625rem;
    margin-top: 2.5rem;
  }
}

@media screen and (max-width: 1500px) {
  .gratitude {
    &__left {
      flex-shrink: 1;
      width: auto;
      padding: 5rem 0 5rem 2.5rem;
    }
    &__right {
      flex-shrink: 0;
      max-width: 32rem;
    }
  }
}

@media screen and (max-width: 1399px) {
  .gratitude {
    h1 {
      font-size: 2.25rem;
    }
    &__text {
      font-size: 1.375rem;
    }
  }
}
@media screen and (max-width: 1180px) {
  .gratitude {
    &__right {
      max-width: 28.125rem;
    }
    h1 {
      font-size: 2rem;
    }
    &__subtitle {
      font-size: 1.25rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .gratitude {
    padding: 11.25rem 0 5rem;
    &__left {
      &:before {
        height: calc(100% + 5rem);
      }
    }
    &__right {
      max-width: 18.75rem;
    }
    h1 {
      font-size: 1.5rem;
    }
    &__text {
      font-size: 1.125rem;
    }
    &__btn {
      margin-top: 1.5rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .gratitude {
    background: #FBF6EF;
    padding: 10rem 0 3.75rem 0;
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      padding: 0;
      margin: 0 0 2.5rem 0;
      &:after {
        display: none;
      }
      &:before {
        display: none;
      }
    }
    &__right {
      max-width: 25rem;
      margin: 0 auto;
    }
    &__desc {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }
}
