.mistake {
  background: #002B45;
  padding: 18.75rem 0 10.625rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: max(6rem, calc((100% - 1623px) / 2));
  }
  &__desc {
    flex-shrink: 0;
    max-width: 41.875rem;
    text-align: center;
  }
  &__img {
    max-width: 55.625rem;
    margin-left: 6.25rem;
    img {
      width: 100%;
    }
  }
  h1 {
    font-family: 'Lora';
    font-size: 23rem;
    line-height: 90%;
    text-transform: uppercase;
    color: rgba(255, 247, 237, 0.36);
    margin: 0;
  }
  &__subtitle {
    font-size: 2.5rem;
    line-height: 130%;
    text-transform: uppercase;
    color: #ffffff;
    margin-top: 3.75rem;
  }
  &__text {
    font-size: 1.5rem;
    line-height: 130%;
    color: #FFF;
    margin-top: 1.5rem;
  }
  &__btn {
    width: 16.25rem;
    height: 3.625rem;
    margin: 3.75rem auto 0;
  }
}

@media screen and (max-width: 1399px) {
  .mistake {
    &__inner {
      padding-left: max(5rem, calc((100% - 1623px) / 2));
    }
    &__img {
      margin-left: 4rem;
    }
    h1 {
      font-size: 15.625rem;
    }
    &__subtitle {
      font-size: 2.25rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .mistake {
    padding: 15.3125rem 0 6.25rem;
    &__inner {
      padding-left: max(3rem, calc((100% - 1623px) / 2));
    }
    h1 {
      font-size: 12.5rem;
    }
    &__subtitle {
      font-size: 2rem;
    }
  }
}

@media screen and (max-width: 1024px) {
  .mistake {
    padding: 10rem 0 3.75rem;
    &__inner {
      display: block;
      padding: 0 2.5rem;
    }
    &__desc {
      max-width: 100%;
    }
    &__img {
      max-width: 25rem;
      margin: 2rem auto 0;
    }
    h1 {
      font-size: 8.75rem;
    }
    &__subtitle {
      font-size: 1.5rem;
      margin-top: 1.5rem;
    }
    &__text {
      font-size: 1.125rem;
    }
    &__btn {
      margin-top: 1.5rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .mistake {
    &__inner {
      padding: 0 2rem;
    }
  }
}

@media screen and (max-width: 499px) {
  .mistake {
    &__inner {
      padding: 0 1.5rem;
    }
  }
}

@media screen and (max-width: 360px) {
  .mistake {
    &__inner {
      padding: 0 1rem;
    }
  }
}