.quiz {
  background: #FFF7ED;
  padding: 17.5rem 0 9.375rem;
  &__inner {
    position: relative;
    background: #FFF;
    box-shadow: 0 0.9375rem 2rem 0 rgba(77, 77, 77, 0.10), 0px 58px 58px 0px rgba(77, 77, 77, 0.09), 0px 131px 79px 0px rgba(77, 77, 77, 0.05), 0px 233px 93px 0px rgba(77, 77, 77, 0.01), 0px 363px 102px 0px rgba(77, 77, 77, 0.00);
    border-radius: 1.25rem;
    overflow: hidden;
  }
  &-pagination {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    &__btn {
      height: 4.6875rem;
      min-width: 8.125rem;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FBF6EF;
      font-family: 'Lora';
      font-size: 2.25rem;
      line-height: 105%;
      font-weight: 700;
      color: #000000;
      padding: 0.1875rem 1rem;
      transition: all 0.3s;
      &:last-child {
        border-radius: 0 1.25rem 0 0;
      }
      span {
        display: none;
        margin-left: 0.5rem;
      }
    }
    &__btn:not(:last-child) {
      border-right: 0.0625rem solid #002B45;
    }
    .pagination-active {
      min-width: 18.75rem;
      color: #ffffff;
      background: #002B45;
      span {
        display: block;
      }
    }
  }
  &-body {
    padding: 1.5rem 9.375rem 3rem;
  }
  &__content {
    display: none;
    margin-top: 1.5rem;
  }
  &__content--active {
    display: block;
  }
  &__subtitle {
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 700;
    color: #000;
  }
  &-plan {
    margin-top: 1.5rem;
  }
  &-plan__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 5.25rem / 4));
    gap: 1.75rem;
  }
  &-plan__card {
    position: relative;
    border: 0.0625rem solid #002B45;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background: #002B45;
      }
    }
    &-img {
      img {
        width: 100%;
      }
    }
    label {
      position: relative;
      width: 2.5rem;
      height: 2.5rem;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #ffffff;
      border-radius: 50%;
      margin: -1.25rem auto 0;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 2rem;
        height: 2rem;
        background: #FFF7ED;
        border: 0.0625rem solid #002B45;
        border-radius: 50%;
        transition: all 0.3s;
      }
    }
    &-desc {
      padding: 0.375rem 1rem 1rem;
    }
    &-title {
      text-align: center;
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 700;
      color: #000;
    }
  }
  &-sizes {
    &__img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 34.375rem;
      pointer-events: none;
    }
  }
  &-sizes__form {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    &-left {
      max-width: 21.875rem;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    &-right {
      max-width: 21.875rem;
      margin-left: 3.75rem;
    }
    &-group:not(:first-child) {
      margin-top: 5rem;
    }
    .input-title {
      display: block;
      font-size: 1.25rem;
      line-height: 130%;
      color: #000;
      margin-bottom: 0.5rem;
    }
    .input {
      display: flex;
      align-items: flex-end;
    }
    .input input {
      font-size: 1.25rem;
      line-height: 130%;
      color: #000;
      background: transparent;
      border: none;
      border-bottom: 0.0625rem solid #000000;
      padding: 0.1875rem;
    }
    .input-value {
      flex-shrink: 0;
      font-size: 1.25rem;
      line-height: 130%;
      color: #000;
      margin-left: 0.3125rem;
    }
    .input-info {
      display: block;
      font-size: 1.25rem;
      line-height: 130%;
      color: #000;
      margin-top: 2rem;
    }
  }
  &-budget {
    margin-top: 5.625rem;
    &__img {
      position: absolute;
      left: 13.75rem;
      bottom: 0;
      width: 33.125rem;
      pointer-events: none;
      opacity: 0.1;
    }
    &__cards {
      display: grid;
      grid-template-columns: repeat(3, calc(100% / 3 - 3rem / 3));
      gap: 1.5rem;
    }
  }
  &-budget__card {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      background: #002B45;
      color: #ffffff;
    }
    label {
      width: 100%;
      min-height: 4.0625rem;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FFF;
      border: 1px solid #002B45;
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 700;
      color: #000;
      padding: 0.3125rem 1rem;
      transition: all 0.3s;
    }
  }
  &-btns {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 0.625rem;
    padding: 0 9.375rem 3.75rem;
  }
  &-btn__prev,
  &-btn__next {
    width: 7.5rem;
    height: 3.4375rem;
  }
  &-btn__prev {
    display: none !important;
    &--active {
      display: block !important;
    }
  }
  &-form {
    display: none;
    align-items: stretch;
    &--active {
      display: flex;
    }
  }
  &-form__desc {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 6.25rem;
  }
  &-form__img {
    width: 50%;
    img {
      width: 100%;
      height: 100%;
      border-radius: 0 1.25rem 1.25rem 0;
    }
  }
  &-form__group {
    margin-top: 5rem;
    input {
      width: 21.25rem;
      height: 3.625rem;
      background: transparent;
      border: 0.0625rem solid #002B45;
      font-size: 1rem;
      line-height: 130%;
      color: #002B45;
      padding: 0.1875rem 1.5rem;
    }
  }
  &-form__socials {
    display: flex;
    align-items: center;
    margin-top: 2rem;
  }
  &-form__socials-text {
    max-width: 14.375rem;
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    color: #000;
    margin-right: 1rem;
  }
  &-form__socials-wrap {
    display: flex;
    align-items: center;
    gap: 0.625rem;
  }
  &-form__social {
    position: relative;
    width: 2.25rem;
    height: 2.25rem;
    svg {
      width: 100%;
      height: 100%;
    }
    svg path {
      transition: all 0.2s;
    }
  }
  .whatsapp {
    &:hover {
      svg path:nth-child(1) {
        fill: #002B45;
      }
      svg path:nth-child(2) {
        fill: #ffffff;
      }
    }
    &:active {
      svg path:nth-child(1) {
        fill: #011A2A;
      }
    }
  }
  .telegram {
    &:hover {
      svg path:nth-child(1) {
        fill: #FBF6EF;
        stroke: #000000;
      }
      svg path:nth-child(2) {
        fill: #002B45 !important;
      }
    }
    &:active {
      svg path:nth-child(1) {
        fill: #000000;
        stroke: #000000;
      }
      svg path:nth-child(2) {
        fill: #ffffff !important;
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  .quiz {
    &-body {
      padding: 1.5rem 3.75rem 3rem;
    }
    &-btns {
      padding: 0 3.75rem 1.5rem;
    }
    &-sizes {
      &__img {
        width: 25rem;
      }
    }
  }
}

@media screen and (max-width: 1180px) {
  .quiz {
    &-pagination {
      &__btn {
        height: 3.75rem;
        min-width: 6.25rem;
        font-size: 1.75rem;
      }
      .pagination-active {
        min-width: 12.5rem;
      }
    }
    &-body {
      padding: 1.5rem 2.5rem 3rem;
    }
    &__subtitle {
      font-size: 1.25rem;
    }
    &-plan__cards {
      grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
      gap: 1.5rem;
    }
    &-plan__card {
      &-title {
        font-size: 1.125rem;
      }
    }
    &-budget {
      margin-top: 2.5rem;
    }
    &-budget__card {
      label {
        font-size: 1.125rem;
      }
    }
    &-btns {
      padding: 0 2.5rem 1.5rem;
    }
    &-sizes__img {
      display: none;
    }
    &-sizes__form {
      .input-title {
        font-size: 1.125rem;
      }
      .input-info {
        font-size: 1.125rem;
      }
      .input input {
        font-size: 1.125rem;
      }
      .input-value {
        font-size: 1.125rem;
      }
    }
    &-form__desc {
      padding: 2.5rem 3.75rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .quiz {
    &-sizes__form {
      display: block;
      &-left {
        max-width: 100%;
      }
      &-right {
        max-width: 100%;
        margin: 3rem 0 0 0;
      }
      &-group:not(:first-child) {
        margin-top: 3rem;
      }
    }
    &-budget__img {
      left: 50%;
      transform: translate(-50%, 0);
      width: 16rem;
    }
    &-budget__cards {
      grid-template-columns: repeat(2, calc(50% - 1.5rem / 2));
    }
    &-form__desc {
      padding: 2.5rem 2rem;
    }
    &-form__group {
      margin-top: 2rem;
      input {
        width: 100%;
      }
    }
    &-form__socials {
      display: block;
    }
    &-form__socials-text {
      margin: 0 0 1rem 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .quiz {
    &-form--active {
      display: block;
    }
    &-form__desc {
      width: 100%;
    }
    &-form__img {
      width: 100%;
      img {
        border-radius: 0 0 1.25rem 1.25rem;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .quiz {
    &-pagination {
      &__btn {
        height: 2.8125rem;
        min-width: 5rem;
        font-size: 1.5rem;
      }
      .pagination-active {
        min-width: auto;
      }
    }
    &-body {
      padding: 1.5rem 2rem;
    }
    &-plan__card {
      &-title {
        font-size: 1rem;
      }
    }
    &-budget__cards {
      grid-template-columns: 100%;
    }
    &-btns {
      padding: 0 2rem 1.5rem;
    }
  }
}

@media screen and (max-width: 499px) {
  .quiz {
    &-plan__cards {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 360px) {
  .quiz {
    &-sizes__form {
      .input input {
        width: 10rem;
      }
    }
  }
}