h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Lora';
}
h2 {
  position: relative;
  z-index: 2;
  display: inline-flex;
  font-size: 2.25rem;
  line-height: 130%;
  font-weight: 700;
  color: #002B45;
  margin: 0;
  span {
    position: absolute;
    top: 1.375rem;
    right: calc(100% + 1.25rem);
    width: 312.5rem;
    height: 0.0625rem;
    background: #002B45;
  }
}
p {
  margin: 0;
}
button {
  font-family: 'OpenSans';
  text-align: center;
  cursor: pointer;
}
.btn {
  span {
    padding: 0;
  }
}
.btn-white__outline {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: transparent;
  border: 0.0625rem solid #FFF;
  color: #FFF;
  cursor: pointer;
  transition: all 0.3s;
  span {
    font-family: 'OpenSans';
    font-size: 1rem;
    line-height: 130%;
    text-transform: uppercase;
  }
  &:hover {
    border-color: #F2A22E;
    color: #F2A22E;
  }
  &:active,
  &:focus {
    border-color: rgba(255, 255, 255, 0.5);
    color: rgba(255, 255, 255, 0.5);
  }
}
.btn-blue__outline {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: transparent;
  border: 0.0625rem solid #002B45;
  color: #002B45;
  cursor: pointer;
  transition: all 0.3s;
  span {
    font-family: 'OpenSans';
    font-size: 1rem;
    line-height: 130%;
    text-transform: uppercase;
  }
  &:hover {
    border-color: #F2A22E;
    color: #F2A22E;
  }
  &:active,
  &:focus{
    border-color: #011A2A;
    color: #011A2A;
  }
}
.btn-blue {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #002B45;
  border: 0.0625rem solid transparent;
  span {
    font-size: 1rem;
    line-height: 130%;
    text-transform: uppercase;
    color: #FBF6EF;
  }
  &:hover {
    background: #015B91;
  }
  &:active,
  &:focus {
    background: #011A2A;
  }
}
.slick-prev {
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: url("../img/prev.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  svg {
    display: none;
  }
}
.slick-next {
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 1.1875rem;
    height: 0.4375rem;
    background-image: url("../img/next.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  svg {
    display: none;
  }
}
.whatsapp {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    bottom: -0.375rem;
    left: -0.18785rem;
    transform: rotate(45deg);
    transition: all 0.3s;
  }
}
.modal {
  background: rgba(#000, .5);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  padding: 6.25rem 0;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: calc(100% - 12rem);
    max-width: 100%;
    position: relative;
    margin: 0 auto;
  }
  &__close{
    position: absolute;
    top: 20px;
    right: 20px;
    background-color: transparent;
    padding: 0;
    svg {
      width: 20px;
      height: 20px;
    }
  }
}
.characteristic {
  display: none;
  position: relative;
  max-width: 112.5rem;
  width: 100%;
  background: #FFF;
  padding: 3.125rem 15.9375rem;
  margin: 3.125rem auto;
  &__inner {
    position: relative;
    z-index: 2;
    max-width: 85rem;
    width: 100%;
    margin: 0 auto;
  }
  &__slider {
    order: 1;
    width: 50%;
  }
  &__desc {
    width: 50%;
    padding: 3.125rem 6.25rem;
    &:after {
      left: 0;
      right: unset;
    }
  }
  &__wrap {
    position: relative;
    z-index: 2;
  }
  &__subtitle {
    position: relative;
    font-size: 1.125rem;
    line-height: 130%;
    margin-top: 1rem;
    padding-bottom: 1.5rem;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      max-width: 21.875rem;
      height: 0.0625rem;
      background: #002B45;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
  }
  &__item {
    position: relative;
    padding: 1.5rem 0;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      max-width: 21.875rem;
      height: 0.0625rem;
      background: #002B45;
    }
    &-price {
      font-family: 'Lora';
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 700;
    }
    ul {
      display: grid;
      grid-template-columns: 100%;
      gap: 0.3125rem;
      margin-top: 0.625rem;
      li {
        font-family: 'Lora';
        font-size: 1rem;
        line-height: 130%;
      }
      li span:nth-child(1) {
        font-weight: 700;
        letter-spacing: 0.2px;
      }
    }
  }
  &__info {
    position: relative;
    display: inline-flex;
    margin-top: 3.75rem;
    span {
      max-width: 13.125rem;
      font-size: 0.875rem;
      line-height: 130%;
    }
    i {
      position: absolute;
      left: 100%;
      bottom: calc(-100% - 1.25rem);
      width: 2.25rem;
    }
    i img {
      width: 100%;
    }
  }
  &__btn {
    width: 7.875rem;
    height: 3.625rem;
    margin-top: 1rem;
  }
  .big-slider {
    .slick-slide:hover {
      .works-card__img:after {
        opacity: 1;
        pointer-events: all;
      }
    }
    .works-card__img {
      padding-bottom: 88%;
    }
  }
  .small-slider {
    width: calc(100% + 5.625rem);
    max-height: 13.125rem;
    padding: 2.1875rem 2.8125rem 2.1875rem 12.5rem;
    margin-left: -5.625rem;
  }
  .works-card__img {
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 3.75rem;
      height: 3.75rem;
      background-image: url("../img/magnifier.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
  }
  .slick-prev {
    width: 3.4375rem;
    left: 2.875rem;
    &:after {
      height: 1.125rem;
    }
  }
  .slick-next {
    width: 3.4375rem;
    left: 6.875rem;
    &:after {
      height: 1.125rem;
    }
  }
  .pagingInfo {
    left: 4.5rem;
    span:nth-child(1) {
      font-size: 3rem;
    }
    span:nth-child(2) {
      font-size: 1.5rem;
    }
  }
  &__img {
    position: absolute;
    top: 50%;
    right: 4.1875rem;
    transform: translate(0, -50%);
    width: 11.6875rem;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 100%;
      object-fit: contain;
    }
  }
}
.error {
  display: none;
  font-size: 1.5rem;
  line-height: 130%;
  font-weight: 700;
  color: #ff0000;
  margin-top: 0.625rem;
  &--active {
    display: block;
  }
}
.text-animation {
  transform: translate(0px, 4rem);
  opacity: 0;
  transition: all 0.4s;
}
.text-animation.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-animation {
  transform: translate(100%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.right-animation.active {
  transform: translate(0,0);
  opacity: 1;
}
.left-animation {
  transform: translate(-100%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.left-animation.active {
  transform: translate(0,0);
  opacity: 1;
}
.opacity-animation {
  opacity: 0;
  transition: all 0.4s;
}
.opacity-animation.active {
  opacity: 1;
}
.top-animation {
  transform: translate(0, -100%);
  opacity: 0;
  transition: all 0.6s;
}
.top-animation.active {
  transform: translate(0,0);
  opacity: 1;
}
.navigation {
  padding: 3.125rem 0;
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5rem;
    a {
      font-size: 1.5rem;
      line-height: 130%;
      &:hover {
        color: #F2A22E;
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  .characteristic {
    padding: 3.75rem 12.5rem 3.75rem 3.75rem;
    &__desc {
      padding: 3.125rem 6.25rem 3.125rem 3.125rem;
    }
    &__img {
      right: 1.25rem;
    }
  }
}

@media screen and (max-width: 1399px) {
  .modal {
    &__dialog {
      width: calc(100% - 10rem);
    }
  }
}

@media screen and (max-width: 1180px) {
  .modal {
    &__dialog {
      width: calc(100% - 6rem);
    }
  }
  .characteristic {
    padding-right: 3.75rem;
    &__img {
      display: none;
    }
  }
}

@media screen and (max-width: 1023px) {
  h2 {
    font-size: 2rem;
    span {
      top: 1.25rem;
    }
  }
  .modal {
    &__dialog {
      width: calc(100% - 5rem);
    }
  }
  .characteristic {
    &-card {
      display: flex;
    }
    &__desc {
      padding: 3.125rem 2.5rem;
    }
    &__info {
      margin-top: 2rem;
    }
    .small-slider {
      width: 100%;
      padding: 2.5rem 2rem 2.5rem 6.625rem;
      margin-left: 0;
    }
    .slick-prev {
      left: 1rem;
      width: 2rem;
    }
    .slick-next {
      left: 3.625rem;
      width: 2rem;
    }
    .pagingInfo {
      left: 1.75rem;
      span:nth-child(1) {
        font-size: 2rem;
      }
      span:nth-child(2) {
        font-size: 1rem;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .modal {
    &__dialog {
      width: calc(100% - 4rem);
    }
  }
  .characteristic {
    padding: 2.5rem 2rem;
    &__desc {
      width: 60%;
      padding: 1.5rem;
    }
    &__slider {
      width: 40%;
    }
    .small-slider {
      padding: 0.75rem 1.25rem 0.75rem 4.6875rem;
    }
    .slick-prev {
      left: 1rem;
      width: 1.1875rem;
      &:after {
        height: 0.4375rem;
      }
    }
    .slick-next {
      left: 2.5rem;
      width: 1.1875rem;
      &:after {
        height: 0.4375rem;
      }
    }
    .pagingInfo {
      left: 1rem;
      span:nth-child(1) {
        font-size: 1.25rem;
      }
      span:nth-child(2) {
        font-size: 0.625rem;
        margin-left: 0.125rem;
      }
    }
    &__subtitle {
      font-size: 1rem;
      padding-bottom: 1rem;
    }
    &__item {
      padding: 1rem 0;
      &-price {
        font-size: 1rem;
      }
      ul {
        li {
          font-size: 1rem;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 1.75rem;
    span {
      top: 1.125rem;
    }
  }
  .characteristic {
    &-card {
      display: block;
    }
    &__slider {
      width: 100%;
    }
    &__desc {
      width: 100%;
      padding: 1rem;
    }
  }
}

@media screen and (max-width: 499px) {
  .modal {
    &__dialog {
      width: calc(100% - 3rem);
    }
  }
  .characteristic {
    padding: 2rem 1.5rem;
    &-card {
      display: block;
    }
    &__desc {
      width: 100%;
      &:after {
        width: 100%;
      }
    }
    &__slider {
      width: 100%;
    }
  }
}

@media screen and (max-width: 359px) {
  .modal {
    &__dialog {
      width: calc(100% - 2rem);
    }
  }
}
